.input{
    padding: 10px 0 10px 15px;
    border-radius: 15px;
    border: none;
    outline: 1px solid var(--bg-secondary-color);
    width: 100%;
    background-color: inherit;
}

.input:hover{
    border: none;
    outline: 2px solid var(--bg-secondary-color);

}
.input:focus{
    border: none;
    outline: 2px solid var(--text-secondary-color);
}
.content{
    width: 100%;
    display: grid;
    align-items: center;

    background-color: transparent;
    border-radius: 20px;
    position: relative;
}

