.btn{
    padding: 10px 18px;
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    border-radius: 55px;
    text-decoration: none;
    display: inline-block;
    border: solid 2px;
    border: 2px solid var(--text-secondary-color);
    margin: 10px;
    cursor: pointer;
}