.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 10;
    background-color: var(--popap-background);

    &__content {
        background-color: var(--bg-main-color);
        padding: 30px;
        border-radius: 20px;
    }
}